<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process189"></a>

<p><br></p>
<p><span class="rvts17">不情愿的仆人（</span><span class="rvts50">The Reluctant Servants</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:538,Y:132</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：守墓人菲尔玛（</span><span class="rvts20">Gravekeeper Farimah</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">杀掉石墓花园的主人瑞克</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1212.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">守墓人菲尔玛一直在石墓花园里面闲逛，玩家可以找到她并与她互动。她会告诉我们石墓花园的领主瑞克有些不太正常，建议杀掉瑞克。</span>
<br><span class="rvts21">强烈建议在完成了瑞克交给的其它两个支线任务后再杀掉瑞克。这样可以获得经验和收成的最大化。</span>
<br><span class="rvts21">杀掉瑞克后，找到守墓人菲儿玛领赏。经测试，只有领赏后杀掉菲儿玛才会使该支线关闭。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



